<template>
    <yd-tabbar :fixed="true" :bgcolor="footerdata.bj" fontsize=".25rem" :footerdata="footerdata">
        <yd-tabbar-item :title="$t('footer.zichan')" type="a"  @click.native="foonav(1)" :class="state==1?'actbj':''">
            <img slot="icon" src="../assets/images/1_on.png" v-if="state==1">
            <img slot="icon" src="../assets/images/1_off.png" v-else>
        </yd-tabbar-item>
        <yd-tabbar-item :title="$t('footer.hangqing')" type="a"  @click.native="foonav(2)" :class="state==2?'actbj':''">
            <img slot="icon" src="../assets/images/2_on.png" v-if="state==2">
            <img slot="icon" src="../assets/images/2_off.png" v-else>
        </yd-tabbar-item>
        <yd-tabbar-item :title="$t('footer.yingyong')" type="a"  @click.native="foonav(3)" :class="state==3?'actbj':''">
            <img slot="icon" src="../assets/images/3_on.png" v-if="state==3">
            <img slot="icon" src="../assets/images/3_off.png" v-else>
        </yd-tabbar-item>
        <yd-tabbar-item :title="$t('footer.faxian')" type="a"  @click.native="foonav(4)" :class="state==4?'actbj':''">
            <img slot="icon" src="../assets/images/4_on.png" v-if="state==4">
            <img slot="icon" src="../assets/images/4_off.png" v-else>
        </yd-tabbar-item>
        <yd-tabbar-item :title="$t('footer.wode')" type="a"  @click.native="foonav(5)" :class="state==5?'actbj':''">
            <img slot="icon" src="../assets/images/5_on.png" v-if="state==5">
            <img slot="icon" src="../assets/images/5_off.png" v-else>
        </yd-tabbar-item>
    </yd-tabbar>
</template>

<script>
    import mapGetters from 'vuex'
export default {
  data () {
    return {
      state:1
    }
  },
  computed:{
    
  },
  props:['footerdata'],
  methods:{
    foonav(state){
        this.$store.commit('change',state)
        this.state=state
        let path="/"
        if (state == 1) {
            path="/"
        }else if (state==2) {
            path="/hangqing"
        }else if (state==3) {
            path="/yingyong"
        }else if (state==4) {
            path="/faxian"
        }else if (state==5) {
            path="/mycenter"
        }
        this.$router.push({path: path,query:{ state:state}});
    }
  },
  created(){
    if (this.$route.query.state) {
        this.state = this.$route.query.state
    }else{
        this.state = this.$store.state.footer_state
    }
    
  },
  mounted(){
        $(".yd-tabbar").css("borderTop","1px solid #f4f4f4")
        var state=this.$route.query.state
        if (state == 1) {
            $(".yd-tabbar").css("borderTop","1px solid #f4f4f4")
        }else if (state==2) {
            $(".yd-tabbar").css("borderTop","1px solid #f4f4f4")
        }else if (state==3) {
            $(".yd-tabbar").css("borderTop","1px solid #f4f4f4")
        }else if (state==4) {
            $(".yd-tabbar").css("borderTop","1px solid #f4f4f4")
        }else if (state==5) {
            $(".yd-tabbar").css("borderTop","1px solid #f4f4f4")
        }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .actbj{
        color: #0e7bda!important;
    }
</style>



