import ajax from '@/utils/requist'

//复制
export function AndroidCopytent(content,callback){
    var iscopy = window.AndroidCopy.copyContent(content)
    callback(iscopy)
}
//传设备ID到后台
export function bindAndriodToken(publickey,andriodToken){
  ajax({
      url:'wallet.ashx',
      method:'post',
      params: {req:8,publickey:publickey,msgToken:andriodToken}
  }).then(res=>{})
}



