<template>
    <div class="header">
        <yd-navbar :title="title.title" :bgcolor="title.bj" fontsize=".36rem" color="#ffffff" height=".9rem">
            <div slot="left" v-if="title.left" @click="goback()">
                <yd-navbar-back-icon color="#ffffff"></yd-navbar-back-icon>
            </div>
            <div slot="left" v-if="title.left_g" @click="gomycenter()">
                <yd-navbar-back-icon color="#ffffff"></yd-navbar-back-icon>
            </div>
            <div slot="right" v-if="title.right">
                <img  v-if="title.addbooklist" class="apply_icon" src="../assets/images/dzb.png" @click="addBookcontrallor()">
                <img  v-if="title.search" class="apply_icon" src="../assets/images/apply.png" @click="apply()">
                <img  v-if="title.addbook" class="apply_icon" src="../assets/images/addbook.png" @click="addBook()">
                <img v-if="title.code_sao" class="apply_icon" src="../assets/images/san.png"  @click="sanwallet()">
                <yd-icon v-if="title.share" name="share3" color="#f5f5f5" size=".45rem"></yd-icon>
                <div v-if="title.save" class="save" @click="save()">{{$t('code.save')}}</div>
            </div>
        </yd-navbar>
    </div>
  
</template>

<script>
export default {
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
  props:['title'],
  methods:{
    goback(){
       this.$router.go(-1)
    },
    //跳转申请
    apply(){
      this.$router.push({path: '/apply'});
    },
    addBook(){
      this.$router.push({path: '/addbook'});
    },
    addBookcontrallor(){
      this.$emit('showaddresBook')
    },
    save(){
      this.$emit('save')
    },
    gomycenter(){
      this.$emit('gomycenter')
    },
    sanwallet(){
      this.$emit('parentsanwallet')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .header{
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        z-index: 9999
    }
    .apply_icon{
      width: .4rem;
      margin-top: .1rem;
    }
    .save{
      color: #fff;
      font-size: .32rem;
    }
    /* .navbar-bottom-line-color:after {
      border-color: #fff !important;
    } */
    .borderb{
      border-bottom: 1px solid #555!important;
    }
</style>



